.container{
    padding-top: 1.5%;
    display:flex;
    margin:auto;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.selectedButton {
    flex:1;
    background-color: #ccc; /*   */
    color: #333; /*    */
    border: none; /*   */
    padding: 8px 16px; /*    */
    margin: 4px; /*    */
    cursor: pointer; /*         */
    border-radius: 3px;
  }
  
  /*   (styles.button) */
  .button {
    flex:1;
    background-color: #f0f0f0; /*     */
    color: #666; /*     */
    border: none; /*   */
    padding: 8px 16px; /*    */
    margin: 4px; /*    */
    cursor: pointer; /*         */
    border-radius: 3px;
  }
  
  /*     */
  .button:hover {
    background-color: #ddd; /*        */
  }
  
  
  
  
  
  
  
