/*   */
.content {
    max-width: 1000px;
    margin: 0 auto;
    padding: 20px;
  }
  
  /*   */
  .title {
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 20px;
  }
  
  /*   */
  .profile {
    border: 1px solid #ccc;
    padding: 15px;
    margin-bottom: 20px;
  }
  
  /*    */
  .detail {
    font-size: 16px;
  }
  
  /*  */
  .id {
    margin-bottom: 5px;
  }
  
  /*  */
  .name {
    margin-bottom: 5px;
  }
  
  /*  */
  .gender {
    margin-bottom: 5px;
  }
  
  /*  */
  .birth {
    margin-bottom: 15px;
  }
  
  /*     */
  .buttons {
    display: flex;
    gap: 10px;
  }
  
  /*    */
  .edit {
    background-color: #4caf50;
    color: white;
    padding: 8px 15px;
    cursor: pointer;
  }
  
  /*    */
  .delete {
    background-color: #f44336;
    color: white;
    padding: 8px 15px;
    cursor: pointer;
  }
  
  /*    */
  .gathering {
    border: 1px solid #ccc;
    padding: 15px;
  }
  
  /*    */
  .gatheringTitle {
    font-size: 20px;
    font-weight: bold;
    margin-bottom: 15px;
  }
  
  /*    */
  .gatheringList {
    cursor: pointer;
    padding: 8px;
    margin-bottom: 5px;
    border: 1px solid #ddd;
    border-radius: 5px;
    transition: background-color 0.3s;
  }
  
  .gatheringList:hover {
    background-color: #f5f5f5;
  }
  